import React from 'react';
import InlineWorker from '../src';
import { Row, Col, Card, Icon, Spin, Divider, Button } from 'antd';
import SyntaxHighlighter from 'react-syntax-highlighter';
import { docco } from 'react-syntax-highlighter/dist/styles/hljs';
import '../style/main.css';
import 'antd/dist/antd.css';

const fn = () => {
	self.onmessage = ({ data }) => {
		var to = setTimeout(() => {
			postMessage('Hello world!');
		}, 1000);
	};
};

export default () => (
	<div>
		<Row>
			<Col span={3}>&nbsp;</Col>
			<Col span={18}>
				<Card
					title={
						<>
							<Icon type="info-circle" theme="twoTone" />
							&nbsp;&nbsp;react-inline-worker
						</>
					}
				>
					<Divider>Example 1</Divider>
					<Row gutter={16}>
						<Col span={12}>
							<Card>1</Card>
						</Col>
						<Col span={12}>
							<Card>
								<InlineWorker
									fn={fn}
									data={{
										var1: 'first variable',
										var2: 'second variable',
									}}
								>
									{({ loading, errors, data }) => {
										if (loading) return <Spin />;
										if (errors) {
											console.error(errors);
											return <div>Errors!</div>;
										}
										return <div>Result from worker: {data}</div>;
									}}
								</InlineWorker>
							</Card>
						</Col>
						<Example
							title="Get a 'Hello world!'"
							description="This is a example"
							code={[
								`import React from 'react';`,
								`import InlineWorker from 'react-inline-worker';`,
								``,
								`function HelloWorldExample(){`,
								`  function fn(){`,
								`    postMessage('Hello world!');`,
								`  }`,
								`  return <InlineWorker fn={HelloWorldExample}>{({ data }) => {`,
								``,
								`  }}</InlineWorker>;`,
								`}`,
								``,
								`export default HelloWorldExample;`,
							].join('\n')}
							result="ahaight"
						/>
						<Example
							title="MyExample2"
							description="This is a example"
							code={`var test2 = false;`}
							result="ahaight"
						/>
						<Example
							title="MyExample3"
							description="This is a example"
							code={`var test3 = "myCode";`}
							result="ahaight"
						/>
					</Row>
				</Card>
			</Col>
			<Col span={3}>&nbsp;</Col>
		</Row>
	</div>
);

function Example({
	title = '',
	description = '',
	code = '',
	result = '',
	loading = false,
}) {
	return (
		<div className="example">
			<Divider>{title}</Divider>
			<Row gutter={16}>
				<Col span={12}>
					<Card>
						<small>{description}</small>
						<div className="block">
							<SyntaxHighlighter language="javascript" style={docco}>
								{code}
							</SyntaxHighlighter>
						</div>
					</Card>
				</Col>
				<Col span={12}>
					<Card>
						<Button type="primary" disabled={loading ? true : false}>
							{loading ? <Icon type="loading" /> : 'Run'}
						</Button>
						<div className="block">{result}</div>
					</Card>
				</Col>
			</Row>
		</div>
	);
}
